// @import url('./vendor/reset.scss');
$dark: #272635; 

.flex-container {
    display: flex;
}
.flex-row {
    flex-direction: row;
}
.flex-col {
    flex-direction: column;
}

.grid-container {
    display: grid;
}
.nav-container {
    margin: 0.5em 0.5em 0.5em 0em;
    width: 100%;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.nav-container .nav-title > a,
.nav-container {
    text-decoration: none;
    color: $dark;
}
.navbar {
    width: 60%;
    padding: 0.25em;
    // justify-content: flex-start;
    > ul {
        display: flex;
        justify-content: space-around;
        flex-direction: row;
        > li {
            list-style-type: none;
            margin-left: 1em;
            > a {
                text-decoration: none;
                color: $dark;
                font-size: 1.1em;
            }
        }
    }
}

.nav-right {
    width: 30%;
    justify-content: space-around;
    > .btn {
        display: inline-flex;
        text-decoration: none;
    }
}
.nav-social {
    margin-top: 0.75em;
    > a {
        font-size: 1.25em;
        margin-left: 1em;
        text-decoration: none;
        color: $dark;
    }    
}

.nav-title {
    display: none;
}
